
.header {
  position: relative;
  width: 100%;
  height: 1rem;
  line-height: 1rem;
  font-size: 0.4rem;
  font-family: 'PingFangSC-Regular';
  text-align: center;
  background-color: #e7001b;
}
.header .header-backicon {
    position: fixed;
    top: .1rem;
    left: .1rem;
    width: .44rem;
    height: .44rem;
    padding: .2rem;
    line-height: .44rem;
    font-size: .44rem;
    vertical-align: middle;
}
.header span.header-title {
    color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 80%;
    display: inline-block;
}
.header .search {
    position: fixed;
    font-size: 20px;
    right: .4rem;
}
.header .search .iconn-4:before {
      color: white;
}
.toSearch {
  position: absolute;
  right: .5rem;
  top: 0;
  font-size: .14rem;
  color: blue;
  font-weight: normal;
}
.toSearch em {
    height: .44rem;
    width: .44rem;
    display: inline-block;
    background: url(../../assets/images/search@3x.png) center center no-repeat;
    background-size: cover;
    position: relative;
    top: .06rem;
}



