
.pageIndex ul {
  margin-top: 1.6rem;
}
.pageIndex ul li {
    text-align: center;
}
.pageIndex ul li div {
      font-family: PingFangSC-Medium;
      font-size: .32rem;
      margin-top: .26rem;
      font-weight: bold;
}
.pageIndex ul li .pic {
      margin: 0 auto;
      width: 2rem;
      height: 2rem;
}
.pageIndex ul li .picSpecial {
      background: url(../../assets/images/special.png) 50% 100% no-repeat;
      background-size: 1.6rem;
}
.pageIndex ul li .picGeneral {
      background: url(../../assets/images/general.png) 50% 100% no-repeat;
      background-size: 1.6rem;
}
.pageIndex ul .general {
    margin-top: 1.2rem;
}
.tips {
  margin-top: 1.84rem;
  padding: 0 .34rem 0 .43rem;
  font-family: PingFangSC-Regular;
  font-size: .26rem;
}
.tips ul li {
    padding-left: .26rem;
    color: #666;
}
.tips ul .title {
    padding-left: 0;
}

