
.box .clips {
  height: .1rem;
  background-color: #f3f5f7;
}
.charts {
  height: 7.8rem;
}
.zxrw {
  font-size: .24rem;
  margin-bottom: .04rem;
}
.list {
  margin-top: .3rem;
  padding-left: .15rem;
  padding-bottom: .3rem;
}
.list li {
    border-bottom: 1px solid #ddd;
}
.list li:first-child {
      border-top: 1px solid #ddd;
}
.list li div {
      height: 1.02rem;
      line-height: 1.02rem;
      font-family: PingFangSC-Regular;
      font-size: .3rem;
      color: #333333;
}
.list li .top-left {
      float: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 5.28rem;
}
.list li .top-left .topName {
        margin-right: .24rem;
}
.list li .top-left span {
        color: #3F3F3F;
}
.list li .top-right {
      float: right;
      margin-right: .28rem;
}
.list li .top-right .topNum {
        font-family: PingFangSC-Regular;
        margin-right: .30rem;
        color: #333;
        font-weight: bold;
}
.list li .top-right .icon:before {
        color: #666;
}
.list .topDate {
    border-bottom: none;
    margin-top: .15rem;
    padding-bottom: 0;
}
.list .topAll {
    border-bottom: none;
    text-align: center;
    font-size: .28rem;
    color: #4A90E2;
    margin-top: .2rem;
    padding-bottom: .28rem;
}
.list .topAll span {
      margin-right: .1rem;
}
.list .topAll .icon {
      font-size: .24rem;
}
.list .topAll .icon:before {
      color: #4A90E2;
}



