
.zxdetail .detailMain .detailtitle {
  display: -webkit-box;
  width: auto;
  position: relative;
  margin: .52rem .3rem 0;
  padding: 0;
  max-height: 1rem;
  overflow: hidden;
}
.zxdetail .detailMain .detailtitle.open {
    max-height: 10rem;
}
.zxdetail .detailMain .detailtitle .detailname {
    text-indent: 0;
    padding-left: .06rem;
    -webkit-box-flex: 1;
    float: none;
    width: auto;
    margin-right: .5rem;
}
.zxdetail .detailMain .detailtitle .detailflag {
    float: none;
    display: inline-block;
    position: relative;
    top: -0.03rem;
}
.zxdetail .detailMain .detailcontainer {
  padding-bottom: .5rem;
}
.zxdetail .detailMain .detailcontainer ol li {
    display: -webkit-box;
    position: relative;
    /*margin-top: .15rem;*/
    margin-bottom: .29rem;
}
.zxdetail .detailMain .detailcontainer ol li.zx_speed {
      margin-bottom: .08rem;
}
.zxdetail .detailMain .detailcontainer ol li .detailexplainItems {
      -webkit-box-flex: 1;
      display: block;
      margin-right: .7rem;
      line-height: .4rem;
      overflow: hidden;
}
.zxdetail .detailMain .detailcontainer ol li .detailexplainItems.margin {
        margin-bottom: .2rem;
        height: .8rem;
}
.zxdetail .detailMain .detailcontainer ol li .detailexplainItems.open {
        height: auto;
}
.zxdetail .detailMain .detailcontainer ol li b {
      font-weight: normal;
      display: block;
      width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #4A90E2;
      height: .6rem;
}
.zxdetail .detailMain .detailcontainer ol li .detailItems {
      line-height: .4rem;
}
.zxdetail .detailMain .detailcontainer ol li .detailexplainItems.specialLi {
      height: auto;
}
.zxdetail .detailMain .detailcontainer ol li div.detailexplainItems {
      padding-top: 0;
      position: relative;
      top: -0.12rem;
}
.zxdetail .detailMain .detailcontainer ol li div.detailexplainItems progress {
        width: 3.9rem;
}
.zxdetail .detailMain .detailcontainer ol li div.detailexplainItems * {
        vertical-align: middle;
}
.zxdetail .detailMain .detailcontainer ol li div.detailexplainItems .detailprogressCount {
        float: none !important;
}
.zxdetail .detailMain .detailcontainer ol li .switchBtn {
      right: .22rem;
}
.zxdetail .detailMain .detailcontainer ol li .switchBtn em {
        position: relative;
        left: .1rem;
}
.zxdetail .detailMain .switchBtn {
  width: .3rem;
  height: .44rem;
  background-size: cover;
  /*background: url(/static/img/up.png) center center no-repeat;*/
  /*background: url(../../assets/images/up.png) center center no-repeat;*/
  /*border-radius: .1rem;*/
  /*border: 1px solid #bebebe;*/
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  display: inline-block;
  position: absolute;
  bottom: unset;
  top: 50%;
  margin-top: -0.32rem;
  right: .02rem;
}
.zxdetail .detailMain .switchBtn em.header-backicon {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    font-size: .2rem;
    position: relative;
    top: .03rem;
}
.zxdetail .detailMain .switchBtn .iconn-1:before {
    color: #666;
}
.zxdetail .detailMain .switchBtn.open {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    bottom: .5rem;
    margin-top: 0;
    top: unset;
}
.switchBtn {
  background: url(../../../assets/images/up.png) center center no-repeat;
}



