
.main {
  font-family: "PingFangSC-Regular";
  text-align: left;
  color: #333;
  font-size: .32rem;
  -webkit-box-flex: 1;
  overflow-y: auto;
  margin-top: 1rem;
}
.main .nav-list ul {
    padding-left: .3rem;
}
.main .nav-list li.mr80 {
    padding-right: .8rem;
}
.main .nav-list li {
    border-bottom: 1px solid #ddd;
    height: 1.2rem;
    line-height: 1.2rem;
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    padding-right: .48rem;
}
.main .nav-list li small {
      font-style: normal;
      color: red;
      font-size: .3rem;
      position: relative;
      top: -.04rem;
}
.main .nav-list li a {
      font-size: .32rem;
      display: block;
}
.main .nav-list li a * {
        vertical-align: middle;
}
.main .nav-list li .icon {
      font-size: .4rem;
      width: .4rem;
      margin: 0 .3rem 0 .15rem;
}
.main .nav-list li .icon-info {
      font-size: .24rem;
      position: absolute;
      right: 0rem;
      top: 50%;
      margin: -0.12rem 0 0 0;
}
.main .nav-list li .iconn-5:before {
      color: #666;
}
.main .nav-list li b {
      position: absolute;
      right: .28rem;
      top: 50%;
      margin-top: -0.12rem;
      height: .24rem;
      width: .13rem;
}
.main .nav-list li u {
      /*height: .34rem;*/
      width: .6rem;
      background-color: #d63333;
      text-align: center;
      color: #fff;
      font-size: .2rem;
      /*padding: .03rem .11rem;*/
      /*padding: .03rem .02rem;*/
      margin-right: .1rem;
      text-decoration: none;
      border-radius: .12rem;
      display: inline-block;
      /*line-height: .34rem;*/
      line-height: normal;
}
.main .nav-list li i {
      font-size: .3rem;
}
.main .nav-list li strong {
      font-size: .28rem;
      color: #666;
      float: right;
      font-weight: normal;
      width: 4.4rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      text-align: right;
}
.main .nav-list .zxlist-li {
    height: 1.5rem;
    /*line-height: 1.5rem;*/
}
.main .nav-list .zxlist-li * {
      vertical-align: middle;
}
.main .nav-list .zxlist-li a {
      display: inline-block;
      width: 98%;
}
.main .nav-list .zxlist-li em.line {
      height: 100%;
      display: inline-block;
}
.main .nav-list .zxlist-li i {
      font-style: normal;
}
.main .nav-list .zxlist-li * {
      vertical-align: middle;
}
.main .nav-list .zxlist-li2 {
    height: 1.5rem;
    line-height: .38rem;
    white-space: normal;
    padding: .25rem 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.main .nav-list .zxlist-li2 * {
      vertical-align: middle;
}
.main .nav-list .zxlist-li2 a {
      display: inline-block;
      width: 98%;
}
.main .nav-list .zxlist-li2 em.line {
      height: 100%;
      display: inline-block;
}
.main .nav-list .zxlist-li2 span {
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      width: 92%;
      /*
        position: relative;
        top: .1rem;*/
}
.main .nav-list .zxlist-li2 span i {
        font-style: normal;
}
.main .nav-list .search-li * {
    vertical-align: middle;
}
.main .nav-list .search-li em {
    height: .32rem;
    width: .32rem;
    display: inline-block;
    margin: 0 .1rem 0 .1rem;
}
.main .nav-list .search-li input[type="text"] {
    color: #333;
    font-size: .32rem;
    width: 90%;
    height: .8rem;
}
.main .nav-list .search-li input[type="text"]::-webkit-input-placeholder {
      color: #999;
}
.main .nav-list .btn-box {
    margin-top: .5rem;
    padding: 0 .3rem;
}
.main .nav-list .btn-box button {
      display: block;
      height: .88rem;
      width: 100%;
      border-radius: .12rem;
      background-color: #bfbfbf;
      color: #fff;
      font-size: .32rem;
}
.main .nav-list .btn-box .on {
      background-color: #d63333;
}
.main .nav-list .clips {
    height: .1rem;
    background-color: #f3f5f7;
}
.main .nav-list .load-more {
    height: .9rem;
    line-height: .9rem;
    color: #999;
    font-size: .3rem;
    text-align: center;
}
.main .nav-list .load-more * {
      vertical-align: middle;
}
.main .nav-list .load-more .icon {
      margin-left: .08rem;
}
.main .nav-list .navListItemTitle {
    color: #b9b9b9;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
    padding-right: .42rem;
}
.main .nav-list .navListItemTitle strong {
      color: #333;
      font-weight: normal;
      margin-right: .16rem;
}
.main .nav-list .searchHistory {
    font-size: .32rem;
    color: #333;
}
.main .nav-list .searchItem {
    color: #666;
    font-size: .28rem;
    line-height: 1.02rem;
    height: 1.02rem;
}
.main .search-head {
    height: .8rem;
    line-height: .8rem;
}
.main .search-head span {
      display: inline-block;
      width: 32%;
      text-align: center;
      height: .3rem;
      line-height: .3rem;
}
.main .search-head .on {
      color: #d63333;
}
.main .search-head .filter {
      border-right: 1px solid #ddd;
}
.header {
  position: fixed !important;
  z-index: 2;
  left: 0;
  top: 0;
}
#app {
  border-top: 1px solid #e7001b;
  display: block;
}
body {
  overflow: auto;
}



