
.errBox {
  text-align: center;
  margin-top: 3rem;
}
.errorIcon {
  display: block;
  width: 3.08rem;
  height: 4.45rem;
  margin: 0 auto;
  background: url(data:image/png;base64,) no-repeat;
}
.errText {
  font-family: PingFangSC-Regular;
  font-size: .28rem;
  color: #666;
  margin-top: .8rem;
}
.retry {
  display: block;
  width: 3rem;
  height: .88rem;
  line-height: .88rem;
  background: #D63333;
  border-radius: 12px;
  color: #fff;
  font-size: .32rem;
  margin: .9rem auto 0 auto;
}



