
.searchBox .searchTop div em.clearText {
  width: .28rem;
  height: .28rem;
  display: inline-block;
  background: url(/static/img/clear@3x.png) center center no-repeat yellow;
}
.searchBox .searchTop div em {
  background: url(../../assets/images/search3x.png) center center no-repeat;
  background-size: cover;
}
.searchInput {
  position: fixed;
  width: 5.9rem;
  height: .6rem;
  line-height: .6rem;
  font-size: .4rem;
  font-family: 'PingFangSC-Regular';
  text-align: center;
  width: 5.9rem;
  height: .6rem;
  z-index: 5;
  left: 1rem;
  top: .2rem;
}
.searchInput form {
    width: 100%;
    height: 100%;
    position: relative;
}
.searchInput form .icon {
      position: absolute;
      right: 0;
      top: 50%;
      margin-top: -0.2rem !important;
}
.searchInput .searchText {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /*padding: 0 .2rem 0 .1rem*/
    padding: 0 .2rem;
    width: 5.9rem;
    height: .6rem;
    border-radius: .14rem;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 0;
}
.searchInput b {
    width: 5.9rem;
    height: .6rem;
    position: absolute;
    left: 0;
    top: 0;
    font-size: .28rem;
    color: #999;
    text-align: left;
    /*background-color: rgba(0,0,0,.4)*/
}
.searchInput b * {
      vertical-align: middle;
}
.searchInput em {
    margin: 0 .2rem;
    background: url(../../assets/images/search3x.png) center center no-repeat;
    background-size: cover;
    width: .28rem;
    height: .28rem;
    display: inline-block;
}
.searchInput i {
    font-weight: normal;
    font-style: normal;
}
.searchBox .searchInput {
  left: 0;
  top: 0;
}



