
.searchBox {
  z-index: 3;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
}
.searchBox .main {
    margin-top: 0;
}
.searchBox .cover {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.7);
}
.searchBox .searchContent {
    position: relative;
    z-index: 4;
}
.searchBox .searchTop {
    height: 1rem;
    line-height: 1rem;
    font-size: 0.4rem;
    font-family: 'PingFangSC-Regular';
    text-align: center;
    background-color: #e7001b;
}
.searchBox .searchTop * {
      vertical-align: middle;
}
.searchBox .searchTop .searchText {
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      /*padding: 0 .2rem 0 .1rem;*/
      padding: 0 .2rem;
      width: 5.9rem;
      height: .6rem;
      /*border-radius: .14rem;*/
      background-color: #fff;
      position: absolute;
      left: 0;
      top: 0;
}
.searchBox .searchTop div {
      position: relative;
      display: inline-block;
      width: 5.9rem;
      height: .6rem;
      line-height: .6rem;
      /*border-radius: .3rem;
      overflow: hidden;*/
}
.searchBox .searchTop div b {
        width: 5.9rem;
        height: .6rem;
        position: absolute;
        left: 0;
        top: 0;
        font-size: .28rem;
        color: #999;
        text-align: left;
}
.searchBox .searchTop div em {
        margin: 0 .2rem;
        width: .28rem;
        height: .28rem;
        display: inline-block;
        background: url(/static/img/search3x.png) center center no-repeat;
}
.searchBox .searchTop div i {
        font-weight: normal;
        font-style: normal;
}
.searchBox .searchTop a {
      font-size: .3rem;
      color: #fff;
      margin-left: .3rem;
}
.searchBox .searchList {
    background-color: #fff;
    padding: .1rem 0 .3rem;
}



