
.special {
  background: #F3F5F7;
  margin-top: 1rem;
}
.special .totalDate {
    text-align: center;
    font-size: .24rem;
    padding-bottom: .4rem;
    color: #999999;
}
.special .total {
    padding: .4rem 0 0 .3rem;
    font-size: .32rem;
    font-weight: bold;
    color: #333;
}
.special div {
    background-color: white;
}
.special .totalBox ul {
    text-align: center;
}
.special .totalBox ul .totalNum {
      font-family: PingFangSC-Medium;
      font-size: .9rem;
      color: #D63333;
}
.special .top10 {
    margin-top: .1rem;
    padding-left: .3rem;
}
.special .top10 h2 {
      font-size: .32rem;
      height: 1.25rem;
      line-height: 1.25rem;
      font-weight: bold;
      color: #333;
      border-bottom: 1px solid #ddd;
}
.special .top10 ul li {
      border-bottom: 1px solid #ddd;
}
.special .top10 ul li div {
        height: 1.02rem;
        line-height: 1.02rem;
        font-family: PingFangSC-Regular;
        font-size: .3rem;
        color: #333333;
}
.special .top10 ul li .top-left {
        float: left;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 5.28rem;
}
.special .top10 ul li .top-left .topName {
          margin-right: .24rem;
}
.special .top10 ul li .top-left span {
          color: #999;
}
.special .top10 ul li .top-right {
        float: right;
        margin-right: .28rem;
}
.special .top10 ul li .top-right .topNum {
          font-family: PingFangSC-Regular;
          margin-right: .30rem;
          color: #333;
          font-weight: bold;
}
.special .top10 ul li .top-right .icon:before {
          color: #666;
}
.special .top10 ul li .top-right em {
          font-size: .24rem;
}
.special .top10 ul .topDate {
      border-bottom: none;
      margin-top: .15rem;
      padding-bottom: 0;
}
.special .top10 ul .topAll {
      border-bottom: none;
      text-align: center;
      font-size: .28rem;
      color: #4A90E2;
      margin-top: .2rem;
      padding-bottom: .28rem;
}
.special .top10 ul .topAll span {
        margin-right: .1rem;
}
.special .top10 ul .topAll .icon {
        font-size: .24rem;
}
.special .top10 ul .topAll .icon:before {
        color: #4A90E2;
}
.special .doneBox {
    margin-top: .1rem;
    padding-left: .3rem;
}
.special .doneBox .chartBox {
      height: 5.08rem;
}
.special .lastSix {
    /*height: 8rem;*/
    margin-top: .1rem;
    padding-bottom: .4rem;
}
.special .lastSix h2 {
      padding: .4rem 0 .4rem .3rem;
      font-size: .32rem;
      font-weight: bold;
      color: #333;
}
.special .lastSix table {
      border-top: 1px solid #DDDDDD;
      margin: 0 .2rem;
      padding-bottom: .5rem;
}
.special .lastSix table tr {
        font-family: PingFangSC-Regular;
        font-size: .3rem;
        color: #333333;
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        border: 1px solid #DDDDDD;
        border-top: none;
        width: 100%;
}
.special .lastSix table .leftData {
        width: 2.48rem;
        border-right: 1px solid #DDDDDD;
}
.special .lastSix table .rightNum {
        width: 4.56rem;
}
.special .zxrw {
    margin-bottom: .04rem;
}
.special .clips {
    height: .2rem;
    background-color: #f3f5f7;
}



