
.none {
  display: none;
}
.riskBox {
  width: 100%;
  overflow: hidden;
  margin-top: 1rem;
}
.tables {
  width: 100%;
  height: 100%;
  font-size: .24rem;
  color: #666;
  font-family: PingFangSC-Regular;
  margin: .2rem .2rem .2rem .2rem;
  width: 7.1rem;
  border: 1px solid #ddd;
}
.tables tr {
    width: 100%;
    height: .8rem;
    line-height: .8rem;
    font-weight: bold;
    text-align: center;
    border-bottom: 1px solid #ddd;
}
.tables .leftHead {
    font-family: PingFangSC-Medium;
    width: 2.6rem;
    color: #333;
    border-right: 1px solid #ddd;
}
.tables .trHead {
    font-family: PingFangSC-Medium;
    color: #333;
    height: .64rem;
    line-height: .64rem;
    background-color: #F5F3F3;
}
.isZl .titleW {
  width: 1.5rem;
}
.isZl .trHead th:last-child {
  padding-right: .2rem;
}
.isZl tr td:last-child {
  padding-right: .2rem;
}
.tbSty tr th:last-child {
  width: 1.6rem;
}
.tbSty tr td:last-child {
  width: 1.6rem;
}
.tipBox {
  margin-left: .2rem;
  margin-top: .2rem;
  font-size: .24rem;
}
.tipBox ul li {
    color: #666;
}
.tipBox .content {
    margin-left: .3rem;
    letter-spacing: .01rem;
    line-height: .4rem;
}
.chart {
  width: 100%;
}
.chart .btnActive {
    color: #D63333;
}
.chart .chartTop {
    width: 100%;
    height: .8rem;
    line-height: .8rem;
    font-size: .32rem;
}
.chart .chartTop span {
      font-family: PingFangSC-Regular;
      display: inline-block;
      width: 31%;
      text-align: center;
}
.chart .chartTop em {
      display: inline-block;
      width: 1px;
      color: #ddd;
}
.chart .grayLine {
    width: 100%;
    height: .1rem;
    background: #F3F5F7;
}
.chart .drawing {
    width: 375px;
    height: 460px;
    margin: 0 auto;
    margin-bottom: 2rem;
}
.bottomBtn {
  font-family: PingFangSC-Regular;
  font-size: .32rem;
  color: #FFFFFF;
  width: 7.5rem;
  height: .8rem;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: center;
}
.bottomBtn .tbBtn, .bottomBtn .graphBtn {
    float: left;
    width: 3.75rem;
    height: .8rem;
    line-height: .8rem;
    background: #000000;
}
.bottomBtn .btnActive {
    background: #D63333;
}
.ztPage {
  width: 9.3rem;
}
.tbBox {
  overflow: auto;
  padding-bottom: 1px;
}
.tbBox .boxW {
    width: 9.7rem;
}
.riskBox td.canClick {
  text-decoration: underline;
  color: blue;
}



