
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
#app {
  height: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
h1 {
  font-size: .2rem;
}
ul li {
  border-radius: 1px;
}
a {
  font-size: .16rem;
}
.noData {
  position: fixed;
  width: 100%;
  top: 50%;
  text-align: center;
  font-size: 20px;
}



