
.detail {
  height: 100%;
}
.detail .detailMain {
    width: 7.5rem;
    margin-top: 1rem;
    overflow: hidden;
}
.detail .detailMain .detailtitle {
      width: 100%;
      padding: .52rem .3rem 0;
      text-align: center;
}
.detail .detailMain .detailtitle .detailflag {
        float: left;
        width: .08rem;
        height: .24rem;
        margin: .14rem 0;
        background-color: #d63333;
}
.detail .detailMain .detailtitle .detailname {
        float: left;
        width: 90%;
        text-indent: .12rem;
        line-height: .5rem;
        font-size: .36rem;
        text-align: left;
}
.detail .detailMain .detailcontainer {
      width: 7rem;
      padding: .22rem .36rem 0;
      overflow: hidden;
}
.detail .detailMain .detailcontainer ol {
        overflow: hidden;
}
.detail .detailMain .detailcontainer ol li {
        position: relative;
        margin-bottom: .29rem;
        /*ie6-ie9*/
}
.detail .detailMain .detailcontainer ol li.disWebkit {
          display: -webkit-box;
}
.detail .detailMain .detailcontainer ol li.disWebkit .detailexplainItems {
            display: block;
}
.detail .detailMain .detailcontainer ol li .detailItems {
          line-height: .4rem;
          font-size: .28rem;
          color: #666;
}
.detail .detailMain .detailcontainer ol li .fl {
          float: left;
}
.detail .detailMain .detailcontainer ol li .detailexplainItems {
          -webkit-box-flex: 1;
          margin-right: .7rem;
          line-height: .4rem;
          overflow: hidden;
}
.detail .detailMain .detailcontainer ol li .detailexplainItems.margin {
            margin-bottom: .2rem;
            height: .8rem;
}
.detail .detailMain .detailcontainer ol li .detailexplainItems.open {
            height: auto;
}
.detail .detailMain .detailcontainer ol li .switchBtn {
          width: .3rem;
          height: .44rem;
          background-size: cover;
          -webkit-transform: rotate(180deg);
                  transform: rotate(180deg);
          display: inline-block;
          position: absolute;
          bottom: unset;
          top: 50%;
          margin-top: -0.32rem;
          right: .22rem;
}
.detail .detailMain .detailcontainer ol li .switchBtn em.header-backicon {
            -webkit-transform: rotate(-90deg);
                    transform: rotate(-90deg);
            font-size: .2rem;
            position: relative;
            top: .03rem;
}
.detail .detailMain .detailcontainer ol li .switchBtn .iconn-1:before {
            color: #666;
}
.detail .detailMain .detailcontainer ol li .switchBtn.open {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
            bottom: .5rem;
            margin-top: 0;
            top: unset;
}
.detail .detailMain .detailcontainer ol li .detailmemberItems {
          float: left;
          line-height: .4rem;
          font-size: .28rem;
          color: #4990e2;
}
.detail .detailMain .detailcontainer ol li .detailprogressCount {
          float: left;
          line-height: .4rem;
          font-size: .28rem;
          color: #f6a623;
          margin-left: .13rem;
}
.detail .detailMain .detailcontainer ol li .detailprogressItems {
          width: 50%;
          height: .1rem;
          margin: .13rem 0;
          background-color: #fff;
          display: inline-block;
          border: 1px solid #f6a623;
          color: #f6a623;
          /*IE10*/
}
.detail .detailMain .detailcontainer ol li progress ie {
          display: block;
          height: 100%;
          background: #f6a623;
}
.detail .detailMain .detailcontainer ol li progress::-moz-progress-bar {
          background: #f6a623;
}
.detail .detailMain .detailcontainer ol li progress::-webkit-progress-bar {
          background: #fff;
}
.detail .detailMain .detailcontainer ol li progress::-webkit-progress-value {
          background: #f6a623;
}
.detail .detailMain .detailcontainer .detailwrapper {
        width: 100%;
        overflow: auto;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-min {
          width: 160%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-mid {
          width: 170%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-max {
          width: 180%;
          height: 5.03rem;
}
.detail .detailMain .detailcontainer .detailwrapper .detaildrawing-smax {
          width: 210%;
          height: 5.03rem;
}
[v-cloak] {
  display: none;
}
.vShow {
  visibility: visible;
}
.vHide {
  visibility: hidden;
}
.detailStars {
  float: left;
  height: .4rem;
  position: relative;
  top: .15rem;
}
.detailStars i {
    height: .4rem;
    width: .4rem;
    display: inline-block;
    verticle-align: middle;
    margin-right: .1rem;
    background: url(../../../assets/images/offstar.png) center center no-repeat;
    background-size: cover;
}
.detailStars i.on {
      background-image: url(../../../assets/images/onstar.png);
}
.detailStars i.half {
      background-image: url(../../../assets/images/halfstar.png);
}



